@import "https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;500&display=swap";
:root {
  --color-bg: #f0e8e3;
  --color-text: #1e1e1e;
  --color-white: #fff;
  --color-green: #24695c;
  --color-orange: #c18e60;
  --color-light-gray: #a8a8a8;
  --color-danger: #b55055;
  --color-danger-text: # #69292c;
  --border-radius: 25px;
  --padding-sm: 2rem 2rem;
  --box-shadow: #0000001a 0px 10px 15px -3px, #0000000d 0px 4px 6px -2px;
  --box-shadow-hover: #00000026 0px 5px 15px 0px;
  --box-shadow-active: #0000001a 0px 1px 3px 0px, #0000000f 0px 1px 2px 0px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  transition: background-color .3s, border .3s;
}

html {
  font-size: 62.5%;
}

::selection {
  background-color: var(--color-green);
  color: var(--color-bg);
}

body {
  background-color: var(--color-bg);
  color: var(--color-text);
  font-family: Outfit, sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  transition: color .3s, background-color .3s;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
}

footer {
  text-align: center;
  padding: 1rem 0;
}

.app-content {
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 6rem 2rem;
  display: flex;
}

.left-content {
  flex-direction: column;
  gap: 2rem;
  display: flex;
}

.form, .right-content, .summary {
  box-shadow: var(--box-shadow);
  padding: var(--padding-sm);
}

.form {
  background-color: var(--color-white);
  border-radius: var(--border-radius);
  flex-direction: column;
  align-items: flex-start;
  gap: 2rem;
  display: flex;
}

.summary {
  background-color: var(--color-white);
  border-radius: var(--border-radius);
}

.summaryGrid {
  grid-gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.summaryGrid p {
  justify-self: end;
  font-size: 2rem;
  font-weight: 500;
}

.totalExpenses {
  border-bottom: 1px solid var(--color-light-gray);
  margin-bottom: 2rem;
}

.right-content {
  background-color: var(--color-white);
  border-radius: var(--border-radius);
  flex-direction: column;
  align-items: flex-start;
  width: 50rem;
  min-height: 50rem;
  display: flex;
}

.history {
  width: 100%;
  margin-bottom: 3rem;
  padding: 0 0 3rem;
}

.history__title {
  padding-bottom: 2rem;
}

.btn {
  cursor: pointer;
  border-radius: var(--border-radius);
  border: none;
  outline: none;
  align-self: center;
  padding: 1rem 4rem;
  font-family: inherit;
  font-size: 1.6rem;
  transition: all .3s;
}

.btn:hover {
  background-color: var(--color-text);
  box-shadow: var(--box-shadow-hover);
  transform: scale(1.08);
}

.btn:active {
  box-shadow: var(--box-shadow-active);
  transform: scale(1);
}

.btn-submit {
  background-color: var(--color-green);
  color: var(--color-bg);
}

.btn-reset {
  background-color: var(--color-orange);
  color: var(--color-bg);
}

.btn-reset:hover {
  background-color: var(--color-danger);
}

.alert {
  background-color: var(--color-danger);
  color: var(--color-bg);
  text-align: center;
  padding: var(--padding-sm);
  border-radius: var(--border-radius);
  margin-bottom: 2rem;
}

.hidden {
  display: none;
}

.form form {
  gap: 3rem;
  display: flex;
}

#history_box {
  flex-direction: column;
  gap: 1rem;
  max-height: 30rem;
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
}

.history__error {
  text-align: center;
}

.history__error p {
  color: var(--color-danger-text);
}

.history__item {
  grid-template-columns: 1.3fr 1.6fr 1fr 1.2fr;
  justify-items: start;
  gap: 1rem;
  display: grid;
}

.date, .category {
  justify-self: start;
}

.amount {
  justify-self: end;
}

.income {
  color: var(--color-green);
}

.expense {
  color: var(--color-orange);
}

fieldset {
  border: none;
  flex-direction: column;
  gap: 1.5rem;
  display: flex;
}

form fieldset div {
  flex-direction: column;
  display: flex;
}

form input, select {
  border: 1px solid var(--color-light-gray);
  border-radius: var(--border-radius);
  outline: none;
  padding: .5rem 2rem;
  font-family: inherit;
  font-size: 1.6rem;
}

input[type="date"] {
  font-size: inherit;
  text-transform: uppercase;
  font-family: inherit;
}

@media only screen and (width <= 75em) {
  .app-content {
    flex-direction: column;
  }
}

@media only screen and (width <= 56.25em) {
  .right-content {
    width: 100%;
    max-width: 58rem;
  }
}

@media only screen and (width <= 37.5em) {
  .form form {
    flex-direction: column;
    gap: 1rem;
  }

  .history__item {
    grid-template-columns: repeat(2, 1fr);
  }

  .purpose {
    justify-self: end;
  }

  .summaryGrid p, .summaryGrid h3 {
    font-size: 1.6rem;
  }

  #history_box {
    gap: 3rem;
  }
}

